@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cinzel:wght@400;500&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto',  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  background: #E5E5E5;
  min-height: 100vh;
  height: 100%;
}

.App_wrap {
  width: 100%;
  max-width: 1095px;
  margin-left: auto;
  margin-right: auto;
}

.Header {
  font-family: 'Cinzel', serif;
  font-style: normal;
  font-weight: 500;
  font-size: 64px;
  line-height: 77px;
  text-align: center;
  text-transform: uppercase;
  margin: 0;
  padding-top: 20px;
  padding-bottom: 20px;
}

.retinueSett {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.att__root {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    width: 100%;
    text-align: center;
}

.att__headCell {
    background: #D32F2F;
    padding-bottom: 5px;
    padding-top: 5px;
}

.att__cell {
    padding-bottom: 7px;
    padding-top: 7px;
}

@media (max-width: 768px) {
    .att__headCell {
        font-size: 14px;
        overflow: hidden;
    }
}


.att__select {
    padding: 0 10px;
    width: 100%;
}

.equip__header {
    padding-top: 5px;
    padding-bottom: 5px;
    font-family: Roboto, sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 29px;
    text-align: center;
    text-transform: uppercase;


    background: #212121;
    color: #FFFFFF;
}

.equip__table {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 3fr;
    width: 100%;
    text-align: center;

}

.table__header {
    background: #D32F2F;
    padding-bottom: 5px;
    padding-top: 5px;
}

.abil__header {
    padding-top: 5px;
    padding-bottom: 5px;
    font-family: Roboto, sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 29px;
    text-align: center;
    text-transform: uppercase;


    background: #212121;
    color: #FFFFFF;
}

.abil__text {
    text-align: center;
    padding-top: 5px;
    padding-bottom: 5px;
}

.solder__root {
    background: rgba(255, 193, 7, 0.65);
}

.soldersButton {
    background: red;
    font-weight: bold;
    font-size: 14px;
    text-transform: uppercase;
    color: white;
    margin-right: auto;
    margin-left: auto;
    display: block;
}

.commander__root {
    background: rgba(255, 193, 7, 0.65);
}

.commander__header {
    padding-top: 10px;
    padding-bottom: 10px;
    font-family: Book Antiqua, Palatino, serif;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 29px;
    text-align: center;
    text-transform: uppercase;


    background: #212121;
    color: #FFFFFF;
}

